import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TABS_DEMO_ROUTES } from './user-role-crud/routes';
import { UserRoleCrudComponent } from './user-role-crud/user-role-crud.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : UserRoleCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class UserRoleRoutingModule{}